'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import { toast } from 'sonner';
import { 
    Search, 
    ShoppingCart, 
    Trash2, 
    Plus, 
    Minus, 
    Banknote, 
    CreditCard, 
    Loader2, 
    CheckCircle2,
    UtensilsCrossed
} from 'lucide-react';

// UI Components
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { ScrollArea } from '@/components/ui/scroll-area'; 
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogFooter,
    DialogDescription
} from '@/components/ui/dialog';
import { Label } from '@/components/ui/label';

// --- TIPE DATA ---
interface Kategori { id: number; nama_kategori: string; }
interface PaymentMethod { id: number; nama_metode: string; is_aktif: boolean; }
interface Produk {
    id: number;
    nama_produk: string;
    harga_jual: number;
    is_tersedia: boolean;
    foto_url: string;
    kategori_id: number;
    kategori?: Kategori;
}

// Tipe Item di Keranjang (Extend Produk dengan qty)
interface CartItem extends Produk {
    qty: number;
}

export default function POSPage() {
    // --- STATE DATA ---
    const [products, setProducts] = useState<Produk[]>([]);
    const [categories, setCategories] = useState<Kategori[]>([]);
    const [payments, setPayments] = useState<PaymentMethod[]>([]);
    const [loading, setLoading] = useState(true);

    // --- STATE FILTER & CART ---
    const [searchQuery, setSearchQuery] = useState('');
    const [selectedCategory, setSelectedCategory] = useState<string>('all');
    const [cart, setCart] = useState<CartItem[]>([]);

    // --- STATE CHECKOUT ---
    const [isCheckoutOpen, setIsCheckoutOpen] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);
    const [selectedPayment, setSelectedPayment] = useState<number | null>(null);
    const [customerName, setCustomerName] = useState('');
    const [cashAmount, setCashAmount] = useState(''); // Uang yang dibayar (string agar bisa kosong)
    const [lastTransaction, setLastTransaction] = useState<any>(null); // Untuk struk sukses

    // 1. FETCH DATA AWAL
    useEffect(() => {
        const initData = async () => {
            try {
                const [resProd, resKat, resPay] = await Promise.all([
                    axios.get('/api/produkRestos'),
                    axios.get('/api/kategoris'),
                    axios.get('/api/paymentMethods'),
                ]);
                setProducts(resProd.data);
                setCategories(resKat.data);
                setPayments(resPay.data.filter((p: PaymentMethod) => p.is_aktif));
            } catch (error) {
                console.error(error);
                toast.error("Gagal memuat data POS.");
            } finally {
                setLoading(false);
            }
        };
        initData();
    }, []);

    // --- LOGIC FORMAT RUPIAH ---
    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    // --- LOGIC FILTER PRODUK ---
    const filteredProducts = products.filter(item => {
        const matchName = item.nama_produk.toLowerCase().includes(searchQuery.toLowerCase());
        const matchCat = selectedCategory === 'all' || item.kategori_id.toString() === selectedCategory;
        return matchName && matchCat && item.is_tersedia; 
    });

    const addToCart = (product: Produk) => {
        setCart(prev => {
            const existing = prev.find(item => item.id === product.id);
            if (existing) {
                return prev.map(item => 
                    item.id === product.id ? { ...item, qty: item.qty + 1 } : item
                );
            }
            return [...prev, { ...product, qty: 1 }];
        });
    };

    const removeFromCart = (id: number) => {
        setCart(prev => prev.filter(item => item.id !== id));
    };

    const updateQty = (id: number, delta: number) => {
        setCart(prev => prev.map(item => {
            if (item.id === id) {
                const newQty = item.qty + delta;
                return newQty > 0 ? { ...item, qty: newQty } : item;
            }
            return item;
        }));
    };

    const totalCartPrice = cart.reduce((sum, item) => sum + (item.harga_jual * item.qty), 0);
    const totalItems = cart.reduce((sum, item) => sum + item.qty, 0);

    // --- LOGIC CHECKOUT ---

    const handleCheckout = async () => {
        if (!selectedPayment) {
            toast.error("Pilih metode pembayaran!");
            return;
        }
        
        const bayar = parseFloat(cashAmount);
        if (isNaN(bayar) || bayar < totalCartPrice) {
            toast.error("Uang pembayaran kurang!");
            return;
        }

        setIsProcessing(true);

        try {
            const payload = {
                payment_method_id: selectedPayment,
                total_bayar: bayar,
                nama_pelanggan: customerName || 'Umum',
                items: cart.map(item => ({
                    produk_resto_id: item.id,
                    jumlah: item.qty
                }))
            };

            const response = await axios.post('/api/transaksiRestos', payload);
            
            // Sukses!
            setLastTransaction(response.data); // Simpan data transaksi untuk struk
            setCart([]); // Kosongkan keranjang
            setCashAmount('');
            setCustomerName('');
            setIsCheckoutOpen(false); // Tutup modal checkout
            
            toast.success("Transaksi Berhasil!", {
                description: `Kembalian: ${formatRupiah(response.data.kembalian)}`
            });

        } catch (error: any) {
            console.error(error);
            toast.error(error.response?.data?.message || "Transaksi Gagal.");
        } finally {
            setIsProcessing(false);
        }
    };

    // --- RENDER ---

    return (
        <div className="flex flex-col lg:flex-row h-[calc(100vh-80px)] gap-4">
            
            {/* === KOLOM KIRI: KATALOG PRODUK === */}
            <div className="flex-1 flex flex-col gap-4 overflow-hidden">
                
                {/* Filter Bar */}
                <div className="bg-white p-4 rounded-xl shadow-sm space-y-4 flex-shrink-0">
                    <div className="flex gap-2 overflow-x-auto pb-2 custom-scrollbar">
                        <Button 
                            variant={selectedCategory === 'all' ? 'default' : 'outline'}
                            onClick={() => setSelectedCategory('all')}
                            className="rounded-full text-xs h-8 whitespace-nowrap bg-[#D93F21] hover:bg-[#b9351b] text-white border-none"
                        >
                            Semua
                        </Button>
                        {categories.map(cat => (
                            <Button
                                key={cat.id}
                                variant={selectedCategory === cat.id.toString() ? 'default' : 'outline'}
                                onClick={() => setSelectedCategory(cat.id.toString())}
                                className={`rounded-full text-xs h-8 whitespace-nowrap ${selectedCategory === cat.id.toString() ? 'bg-gray-800 text-white' : 'hover:bg-gray-100'}`}
                            >
                                {cat.nama_kategori}
                            </Button>
                        ))}
                    </div>
                    <div className="relative">
                        <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
                        <Input 
                            placeholder="Cari menu..." 
                            className="pl-9 bg-gray-50 border-none"
                            value={searchQuery}
                            onChange={e => setSearchQuery(e.target.value)}
                        />
                    </div>
                </div>

                {/* Grid Produk */}
                <div className="flex-1 overflow-y-auto pr-2">
                    {loading ? (
                        <div className="flex justify-center py-10"><Loader2 className="h-8 w-8 animate-spin text-gray-400" /></div>
                    ) : filteredProducts.length === 0 ? (
                        <div className="text-center py-10 text-gray-500">
                            <UtensilsCrossed className="h-12 w-12 mx-auto mb-2 opacity-20" />
                            <p>Menu tidak ditemukan</p>
                        </div>
                    ) : (
                        <div className="grid grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-4">
                            {filteredProducts.map(product => (
                                <Card 
                                    key={product.id} 
                                    className="cursor-pointer hover:shadow-md transition-all border-0 shadow-sm group overflow-hidden"
                                    onClick={() => addToCart(product)}
                                >
                                    <div className="relative h-32 w-full bg-gray-100">
                                        {product.foto_url ? (
                                            <Image 
                                                src={product.foto_url} 
                                                alt={product.nama_produk} 
                                                fill 
                                                className="object-cover group-hover:scale-105 transition-transform duration-300"
                                                unoptimized
                                                onError={(e) => (e.target as HTMLElement).style.display = 'none'}
                                            />
                                        ) : (
                                            <div className="flex items-center justify-center h-full text-gray-300">No IMG</div>
                                        )}
                                        {/* Badge Kategori */}
                                        <Badge className="absolute top-2 left-2 bg-white/90 text-black text-[10px] hover:bg-white backdrop-blur-sm">
                                            {product.kategori?.nama_kategori}
                                        </Badge>
                                    </div>
                                    <div className="p-3">
                                        <h3 className="font-bold text-gray-800 text-sm truncate">{product.nama_produk}</h3>
                                        <p className="text-[#D93F21] font-bold text-sm mt-1">{formatRupiah(product.harga_jual)}</p>
                                    </div>
                                </Card>
                            ))}
                        </div>
                    )}
                </div>
            </div>

            {/* === KOLOM KANAN: KERANJANG (CART) === */}
            <div className="w-full lg:w-[380px] bg-white rounded-xl shadow-sm flex flex-col h-full border border-gray-200">
                <div className="p-4 border-b border-gray-100 flex justify-between items-center bg-gray-50 rounded-t-xl">
                    <h2 className="font-bold text-gray-800 flex items-center">
                        <ShoppingCart className="w-5 h-5 mr-2 text-[#D93F21]" /> Pesanan
                    </h2>
                    <Badge variant="secondary" className="bg-white text-gray-600">
                        {totalItems} Item
                    </Badge>
                </div>

                {/* List Item Cart */}
                <div className="flex-1 overflow-y-auto p-4 space-y-3">
                    {cart.length === 0 ? (
                        <div className="h-full flex flex-col items-center justify-center text-gray-400 text-sm">
                            <ShoppingCart className="h-12 w-12 mb-2 opacity-20" />
                            <p>Keranjang kosong</p>
                        </div>
                    ) : (
                        cart.map(item => (
                            <div key={item.id} className="flex items-center gap-3 bg-gray-50 p-2 rounded-lg group">
                                <div className="flex-1">
                                    <p className="text-sm font-semibold text-gray-800 truncate">{item.nama_produk}</p>
                                    <p className="text-xs text-[#D93F21] font-medium">{formatRupiah(item.harga_jual * item.qty)}</p>
                                </div>
                                <div className="flex items-center bg-white rounded-md shadow-sm border border-gray-100">
                                    <button 
                                        onClick={() => item.qty === 1 ? removeFromCart(item.id) : updateQty(item.id, -1)}
                                        className="p-1.5 hover:bg-red-50 text-gray-600 hover:text-red-600 rounded-l-md transition-colors"
                                    >
                                        {item.qty === 1 ? <Trash2 className="w-3.5 h-3.5" /> : <Minus className="w-3.5 h-3.5" />}
                                    </button>
                                    <span className="w-8 text-center text-xs font-bold">{item.qty}</span>
                                    <button 
                                        onClick={() => updateQty(item.id, 1)}
                                        className="p-1.5 hover:bg-green-50 text-gray-600 hover:text-green-600 rounded-r-md transition-colors"
                                    >
                                        <Plus className="w-3.5 h-3.5" />
                                    </button>
                                </div>
                            </div>
                        ))
                    )}
                </div>

                {/* Footer Cart */}
                <div className="p-4 bg-gray-50 border-t border-gray-200 rounded-b-xl space-y-4">
                    <div className="space-y-1">
                        <div className="flex justify-between text-sm text-gray-600">
                            <span>Subtotal</span>
                            <span>{formatRupiah(totalCartPrice)}</span>
                        </div>
                        <div className="flex justify-between text-base font-bold text-gray-900 pt-2 border-t border-gray-200">
                            <span>Total Bayar</span>
                            <span className="text-[#D93F21] text-xl">{formatRupiah(totalCartPrice)}</span>
                        </div>
                    </div>
                    
                    <Button 
                        className="w-full bg-gray-900 hover:bg-black text-white py-6 text-lg font-bold shadow-lg"
                        disabled={cart.length === 0}
                        onClick={() => setIsCheckoutOpen(true)}
                    >
                        Bayar Sekarang
                    </Button>
                </div>
            </div>

            {/* === MODAL CHECKOUT === */}
            <Dialog open={isCheckoutOpen} onOpenChange={setIsCheckoutOpen}>
                <DialogContent className="sm:max-w-[500px]">
                    <DialogHeader>
                        <DialogTitle className="text-center text-xl">Pembayaran</DialogTitle>
                        <DialogDescription className="text-center">
                            Total Tagihan: <span className="font-bold text-gray-900 text-lg">{formatRupiah(totalCartPrice)}</span>
                        </DialogDescription>
                    </DialogHeader>
                    
                    <div className="grid gap-4 py-4">
                        <div className="space-y-2">
                            <Label>Nama Pelanggan (Opsional)</Label>
                            <Input 
                                placeholder="Cth: Pak Budi" 
                                value={customerName}
                                onChange={e => setCustomerName(e.target.value)}
                            />
                        </div>

                        <div className="space-y-2">
                            <Label>Metode Pembayaran</Label>
                            <div className="grid grid-cols-3 gap-2">
                                {payments.map(method => (
                                    <div 
                                        key={method.id}
                                        onClick={() => setSelectedPayment(method.id)}
                                        className={`cursor-pointer rounded-lg border p-3 flex flex-col items-center justify-center gap-2 transition-all ${
                                            selectedPayment === method.id 
                                                ? 'border-[#D93F21] bg-red-50 text-[#D93F21]' 
                                                : 'border-gray-200 hover:border-gray-300'
                                        }`}
                                    >
                                        {method.nama_metode.toLowerCase().includes('cash') ? (
                                            <Banknote className="h-5 w-5" />
                                        ) : (
                                            <CreditCard className="h-5 w-5" />
                                        )}
                                        <span className="text-xs font-medium text-center">{method.nama_metode}</span>
                                    </div>
                                ))}
                            </div>
                        </div>

                        <div className="space-y-2">
                            <Label>Uang Diterima</Label>
                            <Input 
                                type="number" 
                                placeholder="Masukkan jumlah uang..." 
                                value={cashAmount}
                                onChange={e => setCashAmount(e.target.value)}
                                className="text-lg font-mono"
                            />
                            {/* Quick Amount Buttons */}
                            <div className="flex gap-2 mt-2 overflow-x-auto pb-1">
                                {[totalCartPrice, 50000, 100000].map(amt => (
                                    amt >= totalCartPrice && (
                                        <Button 
                                            key={amt} 
                                            type="button" 
                                            variant="outline" 
                                            size="sm" 
                                            className="text-xs"
                                            onClick={() => setCashAmount(amt.toString())}
                                        >
                                            {formatRupiah(amt)}
                                        </Button>
                                    )
                                ))}
                            </div>
                        </div>

                        {/* Info Kembalian */}
                        <div className="bg-gray-100 p-3 rounded-lg flex justify-between items-center">
                            <span className="text-sm font-medium text-gray-600">Kembalian</span>
                            <span className={`text-lg font-bold ${
                                (parseFloat(cashAmount) || 0) >= totalCartPrice ? 'text-green-600' : 'text-gray-400'
                            }`}>
                                {formatRupiah(Math.max(0, (parseFloat(cashAmount) || 0) - totalCartPrice))}
                            </span>
                        </div>
                    </div>

                    <DialogFooter>
                        <Button 
                            className="w-full bg-[#D93F21] hover:bg-[#b9351b] py-6 text-lg" 
                            onClick={handleCheckout}
                            disabled={isProcessing}
                        >
                            {isProcessing ? <Loader2 className="h-5 w-5 animate-spin" /> : "Proses Transaksi"}
                        </Button>
                    </DialogFooter>
                </DialogContent>
            </Dialog>

            {/* === DIALOG SUKSES (STRUK SEDERHANA) === */}
            {lastTransaction && (
                <Dialog open={!!lastTransaction} onOpenChange={() => setLastTransaction(null)}>
                    <DialogContent className="sm:max-w-sm text-center">
                        <div className="mx-auto w-12 h-12 bg-green-100 rounded-full flex items-center justify-center mb-4">
                            <CheckCircle2 className="h-6 w-6 text-green-600" />
                        </div>
                        <DialogTitle className="text-center">Transaksi Berhasil!</DialogTitle>
                        <DialogDescription className="text-center">
                            Struk telah dibuat dengan ID: <br/>
                            <span className="font-mono font-bold text-gray-900">{lastTransaction.kode_transaksi}</span>
                        </DialogDescription>
                        
                        <div className="py-4 border-t border-b border-dashed my-4 space-y-2 text-sm">
                            <div className="flex justify-between">
                                <span className="text-gray-500">Total</span>
                                <span className="font-bold">{formatRupiah(lastTransaction.total_harga)}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-500">Bayar</span>
                                <span>{formatRupiah(lastTransaction.total_bayar)}</span>
                            </div>
                            <div className="flex justify-between text-green-600 font-bold">
                                <span>Kembalian</span>
                                <span>{formatRupiah(lastTransaction.kembalian)}</span>
                            </div>
                        </div>

                        <Button onClick={() => setLastTransaction(null)} className="w-full">
                            Transaksi Baru
                        </Button>
                    </DialogContent>
                </Dialog>
            )}
        </div>
    );
}